/* src/styles/Taskbar.css */

.taskbar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 48px;
  
  /* Windows 11 Dark Theme Color */
  background: rgba(32, 32, 32, 0.95); 
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  
  display: flex;
  justify-content: space-between; /* Left(Empty) - Center(Icons) - Right(Tray) */
  align-items: center;
  padding: 0 15px;
  z-index: 9999;
}

/* Center Icons Group */
.taskbar-center {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 8px;
  height: 100%;
  align-items: center;
}

.tb-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
}

.tb-icon:hover {
  background-color: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

.tb-icon img {
  width: 24px;
  height: 24px;
}

/* Active Indicator (little line at bottom) */
.tb-icon.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  width: 12px;
  height: 3px;
  background-color: #0078d4; /* Windows Blue */
  border-radius: 2px;
}

/* System Tray (Right) */
.system-tray {
  margin-left: auto; /* Push to right */
  display: flex;
  align-items: center;
  gap: 15px;
  color: white;
}

.tray-icon {
  font-size: 16px;
  opacity: 0.8;
}

.clock {
  text-align: right;
  font-size: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: default;
}

.clock:hover {
  background-color: rgba(255,255,255,0.1);
  border-radius: 4px;
  padding: 2px 5px;
}